* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.App {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.box {
  background-color: lightblue;
  /* background-image: url(https://i.pinimg.com/564x/91/3c/b7/913cb7eb165777e28d0769c97ccf57d9.jpg); */
  width: 25vw;
  height: 60vh;
  padding-top: 50px;
  box-shadow: 5px 5px 8px lightslategray;
  border-radius: 0.5rem;
  position: relative;
  overflow: hidden;
  transform: translate3d(0, 0, 0);
  min-width: 20rem;
  min-height: 35rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: space-between; */
}

.inputField {
  border: none;
  padding: 10px;
  width: 20vw;
  min-width: 16rem;
  text-align: center;
  font-size: 20px;
  border-radius: 30px;
}

p {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 30px;
  margin-top: 50px;
  font-weight: bold;
  color: grey;
}

.info {
  z-index: 5;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: absolute;
  bottom: 20px;
}

i {
  margin-right: 10px;
  font-size: 70px;
  color: white;
}

h2 {
  font-size: 50px;
}

h3 {
  font-size: 25px;
}

.location {
  font-size: 35px;
  display: flex;
}

.tempmin_max {
  color: grey;
}

.wave {
  opacity: 0.3;
  position: absolute;
  top: 120%;
  left: 50%;
  background: white;
  width: 50rem;
  height: 50rem;
  margin-left: -25rem;
  margin-top: -20rem;
  transform-origin: 50% 48%;
  border-radius: 43%;
  animation: drift 3000ms infinite linear;
  z-index: 1;
}

.wave.-three {
  /* background-color: lawngreen; */
  animation: drift 5000ms infinite linear;
  z-index: 2 !important;
  opacity: 0.4;
}

.wave.-two {
  /* background-color: lightcoral; */
  animation: drift 7000ms infinite linear;
  z-index: 3 !important;
  opacity: 0.1;
}

.box:after {
  content: "";
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

@keyframes drift {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}
